.list {
	display: flex;
	flex-wrap: wrap;
	text-align: center;
	margin: auto;
	justify-content: space-between;
	gap: 20px;
	margin-left: 60px;
	margin-right: 60px;
}
.list li {
	list-style: none;
	flex: 1 1 0;
	font-size: 16px;
}
.list li span {
	display: block;
}
.modalName {
	display: block;
	text-align: center;
}
.modalImage {
	max-width: 100%;
	display: block;
	margin: auto;
}
@media (max-width: 991.9px) {
	.list {
		padding-left: 0;
		padding-right: 0;
		margin-left: 34px;
		margin-right: 34px;
		gap: 16px;
	}
}
@media (max-width: 575.9px) {
	.container {
		padding-left: 0;
		padding-right: 0;
	}
	.list li {
		font-size: 14px;
	}
	.list li span {
		font-size: 22px;
	}
	.modalName {
		font-size: 20px;
	}
}
