.menu {
	list-style: none;
	padding-top: 140px;
}

.menuItem {
	display: block;
	text-decoration: none;
	color: #8585ad;
	transition: color 0.2s ease;
	width: fit-content;
}
.menuItem:hover {
	color: #fff;
}
.menuItem.menuItemActive {
	color: #fff;
}
.container {
	max-width: 1280px;
	width: 100%;
	margin: 0 auto;
	display: flex;
}
.navWrapper {
	margin-right: 60px;
	max-width: 320px;
	flex-basis: 320px;
}
.tabContentWrapper {
	padding-top: 40px;
	flex-grow: 1;
	width: 100%;
}
@media screen and (max-width: 991.9px) {
	.navWrapper {
		display: none;
	}
	.tabContentWrapper {
		padding-inline: 8px;
	}
}
